<template>
  <div class="card border-0 shadow-sm mb-4 p-3" v-if="loggIn">
    <nav class="nav nav-pills nav-fill">
      <a
        class="nav-item nav-link active"
        id="pills-text-tab"
        data-toggle="pill"
        href="#pills-text"
        role="tab"
        aria-controls="pills-text"
        aria-selected="true"
        @click="reset"
        ><i class="fas fa-file-alt"></i> Texto</a
      >
      <a
        class="nav-item nav-link"
        id="pills-image-tab"
        data-toggle="pill"
        href="#pills-image"
        role="tab"
        aria-controls="pills-image"
        aria-selected="false"
        @click="reset"
        ><i class="fas fa-image"></i> Imagen</a
      >
      <a
        class="nav-item nav-link"
        id="pills-video-tab"
        data-toggle="pill"
        href="#pills-video"
        role="tab"
        aria-controls="pills-video"
        aria-selected="false"
        @click="reset"
        ><i class="fas fa-camera-retro"></i> Video</a
      >
    </nav>
    <div class="tab-content pt-4" id="pills-tabContent">
      <div
        class="tab-pane fade show active"
        id="pills-text"
        role="tabpanel"
        aria-labelledby="pills-text-tab"
      >
        <form @submit.prevent="onSubmitText">
          <div class="form-group">
            <textarea
              class="form-control"
              v-model="form.content"
              id="text"
              rows="3"
              placeholder="Escribe tus ideas..."
            ></textarea>
          </div>
          <button type="submit" class="btn btn-primary btn-block">
            Enviar
          </button>
        </form>
      </div>
      <div
        class="tab-pane fade"
        id="pills-image"
        role="tabpanel"
        aria-labelledby="pills-image-tab"
      >
        <form @submit.prevent="onSubmitImage">
          <div class="custom-file mb-3">
            <input
              type="file"
              class="custom-file-input"
              id="customFile"
              lang="es"
            />
            <label
              class="custom-file-label"
              for="customFile"
              data-browse="Buscar"
              >Cargar imagen</label
            >
          </div>
          <div class="form-group">
            <textarea
              class="form-control"
              v-model="form.content"
              id="text"
              rows="3"
              placeholder="Comentar tu imagen..."
            ></textarea>
          </div>
          <button type="submit" class="btn btn-primary btn-block">
            Enviar
          </button>
        </form>
      </div>
      <div
        class="tab-pane fade"
        id="pills-video"
        role="tabpanel"
        aria-labelledby="pills-video-tab"
      >
        <form @submit.prevent="onSubmitVideo">
          <div class="form-group">
            <input
              type="text"
              class="form-control"
              v-model="form.video"
              id="video"
              placeholder="Link del video de Youtube"
            />
          </div>
          <div class="form-group">
            <textarea
              class="form-control"
              v-model="form.content"
              id="text"
              rows="3"
              placeholder="Escribe sobre el video..."
            ></textarea>
          </div>
          <button type="submit" class="btn btn-primary btn-block">
            Enviar
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { fb } from "@/firebase";

export default {
  name: "Publisher",
  props: {},
  data() {
    return {
      form: {
        content: "",
        image: "",
        video: "",
        page: this.$route.params.id
      },
      loggIn: false
    };
  },
  created() {
    this.loginCheck();
  },
  methods: {
    ...mapActions(["addPost"]),
    onSubmitText() {
      this.addPost(this.form);

      this.reset();
      this.$route.redirect(`/${this.page}`);
    },
    onSubmitImage() {
      this.addPost(this.form);

      this.reset();
      this.$route.redirect(`/${this.page}`);
    },
    onSubmitVideo() {
      this.addPost(this.form);

      this.reset();
      this.$route.redirect(`/${this.page}`);
    },
    loginCheck() {
      fb.auth().onAuthStateChanged(user => {
        if (user) {
          this.loggIn = true;
        } else {
          this.loggIn = false;
        }
      });
    },
    reset() {
      this.form.content = "";
      this.form.image = "";
      this.form.video = "";
    }
  },
  computed: {}
};
</script>

<style scoped></style>
